import React, { FC, useEffect, useState } from 'react';
import MenuSection from '../MenuSection/MenuSection.lazy';
import './MyActivity.scss';



interface MyActivityProps {}

interface StackOverflow{
  timeline: string,
  title: string,
  uri: string,
  tags: string[]
}


const redirectToLink=(url:string):any =>{
  const win = window.open("https://stackoverflow.com"+url, '_blank');
  if (win != null) {
    win.focus();
  }
  return win;
}

function loaderGithubData(): JSX.Element{
  return  <div className="loading-dim">
    <div className="loader2"></div>
    <div className="loader2"></div>
    <div className="loader2"></div>
  </div>  
}

function displayStackData(stackoverflowData: StackOverflow[]): JSX.Element[]{
 let id=0;
 let image= "/assets/brand-logo/StackOverflow-Logo.svg"
  return  stackoverflowData.map((stackQuesList: any) => {
    stackQuesList.id=id+=1;
    const styles: React.CSSProperties = {
      backgroundImage: `url(${image})`,
      backgroundSize: `contain`  ,
      backgroundPositionX: `left`
    }
    
    return (
      <div
        key={stackQuesList.id}
        className="stackOverFlow-card background-image"
        style={styles}
        onClick={(event: React.MouseEvent<HTMLElement>) => {redirectToLink(stackQuesList.uri)}}
      >
        <div className="stackOverFlow-card-content">
          <div className="stackOverFlow-card-content-items">
            <span className="stackOverFlow-card-title">
              {"stackQuesList.title".length > 100
                ? stackQuesList.title.substring(0, 100) + "..."
                : stackQuesList.title}
            </span>
            <span className="stackOverFlow-card-desc">
              {stackQuesList.tags.join(", ")}
            </span>
          </div>
        </div>
      </div>
    );
  });
}


